                <span class="date">{{ formatDate(item.updatedAt || item.createdAt) }}</span>
                <span class="views-count">{{ formatNumber(item.views || 0) }} æµè§ˆ</span>
              </div>
              
              <button class="btn" @click.stop="viewContent(item)">æŸ¥çœ‹è¯¦æƒ…</button>
