          </div>
          
          <!-- åˆ†é¡µæ§ä»¶ -->
          <div v-if="totalPages > 1" class="pagination">
            <button 
              class="pagination-btn" 
              :disabled="currentPage === 1"
              @click="prevPage"
            >
              ä¸Šä¸€é¡µ
            </button>
            
            <span class="pagination-info">ç¬¬ {{ currentPage }} é¡µï¼Œå…± {{ totalPages }} é¡µ</span>
            
            <button 
              class="pagination-btn" 
              :disabled="currentPage === totalPages"
              @click="nextPage"
            >
              ä¸‹ä¸€é¡µ
            </button>
