<script>
import { getCategories, getTopicsByCategory } from '../api/topics';
import { getBlogList } from '../api/blog';

export default {
  name: 'CategoryPage',
  data() {
    return {
      // å®Œæ•´çš„åˆå§‹åˆ†ç±»æ•°æ®
      categories: [
        { id: 1, name: 'æŠ€æœ¯åˆ†äº«', description: 'åˆ†äº«å„ç±»æŠ€æœ¯çŸ¥è¯†å’Œç»éªŒ', icon: 'ğŸ’»', color: '#4a90e2', postCount: 42, activeUsers: 128 },
        { id: 2, name: 'ç”Ÿæ´»éšç¬”', description: 'è®°å½•ç”Ÿæ´»ä¸­çš„ç‚¹ç‚¹æ»´æ»´', icon: 'ğŸ“', color: '#52c41a', postCount: 28, activeUsers: 89 },
        { id: 3, name: 'èŒåœºç»éªŒ', description: 'èŒåœºå¿ƒå¾—ä¸èŒä¸šå‘å±•å»ºè®®', icon: 'ğŸ’¼', color: '#faad14', postCount: 36, activeUsers: 156 },
        { id: 4, name: 'å…´è¶£çˆ±å¥½', description: 'ä¸ªäººå…´è¶£ä¸çˆ±å¥½äº¤æµ', icon: 'ğŸ®', color: '#f5222d', postCount: 51, activeUsers: 203 },
        { id: 5, name: 'å­¦ä¹ èµ„æº', description: 'ä¼˜è´¨å­¦ä¹ èµ„æ–™ä¸æ•™ç¨‹åˆ†äº«', icon: 'ğŸ“š', color: '#1890ff', postCount: 33, activeUsers: 178 },
        { id: 6, name: 'å¥åº·ç”Ÿæ´»', description: 'å¥åº·é¥®é£Ÿä¸ç”Ÿæ´»æ–¹å¼', icon: 'ğŸƒ', color: '#52c41a', postCount: 24, activeUsers: 92 },
        { id: 7, name: 'æ—…è¡Œè§é—»', description: 'æ—…è¡Œç»å†ä¸æ™¯ç‚¹æ¨è', icon: 'âœˆï¸', color: '#fa8c16', postCount: 47, activeUsers: 145 },
        { id: 8, name: 'ç§‘æŠ€å‰æ²¿', description: 'æœ€æ–°ç§‘æŠ€åŠ¨æ€ä¸è¶‹åŠ¿', icon: 'ğŸš€', color: '#722ed1', postCount: 38, activeUsers: 167 }
      ],
      topics: [],
      blogs: [],
      selectedCategoryId: null,
      categoryName: '',
      loading: false,
      // ç­›é€‰å’Œæ’åºé€‰é¡¹
      filters: {
        contentType: 'all', // all, blog, topic
        search: '' // æœç´¢å…³é”®è¯
      },
      sortBy: 'newest', // newest, oldest, mostViewed, mostComments
      // åˆ†é¡µç›¸å…³
      currentPage: 1,
      pageSize: 10
    };
  },
  computed: {
    // å½“å‰é€‰ä¸­çš„åˆ†ç±»ä¿¡æ¯
    currentCategory() {
      return this.categories.find(cat => cat.id === this.selectedCategoryId);
    },
    
    // è·å–å½“å‰é€‰ä¸­çš„åˆ†ç±»ä¿¡æ¯ (æ–°å¢)
    selectedCategory() {
      return this.categories.find(cat => cat.id === this.selectedCategoryId) || null;
    },
    
    // å†…å®¹æ€»æ•°ç»Ÿè®¡
    totalContents() {
      return this.filteredContents.length;
    },
    
    // åšå®¢æ–‡ç« æ•°é‡
    blogCount() {
      return this.filteredContents.filter(item => item.type === 'blog').length;
    },
    
    // è¯é¢˜è®¨è®ºæ•°é‡
    topicCount() {
      return this.filteredContents.filter(item => item.type === 'topic').length;
    },
    
    // æ€»æµè§ˆé‡
    totalViews() {
      return this.filteredContents.reduce((total, item) => total + (item.views || 0), 0);
    },
    
    // åˆå¹¶å¹¶å¤„ç†å†…å®¹ï¼ˆåº”ç”¨ç­›é€‰å’Œæ’åºï¼‰
    filteredContents() {
      // å°†åšå®¢æ–‡ç« è½¬æ¢ä¸ºç»Ÿä¸€æ ¼å¼
      const formattedBlogs = this.blogs.map(blog => ({
        id: blog.id,
        type: 'blog',
        title: blog.title,
        content: blog.content,
        author: blog.author,
        category: blog.category,
        tags: blog.tags || [],
        source: blog.source,
        views: blog.views || 0,
        comments: blog.comments || 0,
        createdAt: blog.createdAt,
        updatedAt: blog.updatedAt
      }));
      
      // å°†è¯é¢˜è½¬æ¢ä¸ºç»Ÿä¸€æ ¼å¼
      const formattedTopics = this.topics.map(topic => ({
        id: topic.id,
        type: 'topic',
        title: topic.title,
        category: topic.category,
        source: topic.source,
        views: topic.views,
        comments: topic.comments,
        createdAt: topic.createdAt || topic.updatedAt,
        updatedAt: topic.updatedAt
      }));
      
      // åˆå¹¶å†…å®¹
      let allContents = [...formattedBlogs, ...formattedTopics];
      
      // åº”ç”¨å†…å®¹ç±»å‹ç­›é€‰
      if (this.filters.contentType !== 'all') {
        allContents = allContents.filter(item => item.type === this.filters.contentType);
      }
      
      // åº”ç”¨æœç´¢ç­›é€‰
      if (this.filters.search.trim()) {
        const searchTerm = this.filters.search.toLowerCase().trim();
        allContents = allContents.filter(item => 
          item.title.toLowerCase().includes(searchTerm) ||
          (item.content && item.content.toLowerCase().includes(searchTerm)) ||
          (item.tags && item.tags.some(tag => tag.toLowerCase().includes(searchTerm)))
        );
      }
      
      // åº”ç”¨æ’åº
      switch (this.sortBy) {
        case 'newest':
          allContents.sort((a, b) => {
            const dateA = new Date(a.updatedAt || a.createdAt);
            const dateB = new Date(b.updatedAt || b.createdAt);
            return dateB - dateA;
          });
          break;
        case 'oldest':
          allContents.sort((a, b) => {
            const dateA = new Date(a.updatedAt || a.createdAt);
            const dateB = new Date(b.updatedAt || b.createdAt);
            return dateA - dateB;
          });
          break;
        case 'mostViewed':
          allContents.sort((a, b) => (b.views || 0) - (a.views || 0));
          break;
        case 'mostComments':
          allContents.sort((a, b) => (b.comments || 0) - (a.comments || 0));
          break;
        default:
          // é»˜è®¤æŒ‰æœ€æ–°æ’åº
          allContents.sort((a, b) => {
            const dateA = new Date(a.updatedAt || a.createdAt);
            const dateB = new Date(b.updatedAt || b.createdAt);
            return dateB - dateA;
          });
      }
      
      return allContents;
    },
    
    // åˆ†é¡µåçš„å†…å®¹
    paginatedContents() {
      const startIndex = (this.currentPage - 1) * this.pageSize;
      const endIndex = startIndex + this.pageSize;
      return this.filteredContents.slice(startIndex, endIndex);
    },
    
    // æ€»é¡µæ•°
    totalPages() {
      return Math.ceil(this.filteredContents.length / this.pageSize);
    }
  },
  created() {
    // ç”±äºcategorieså·²ç»æœ‰äº†åˆå§‹æ•°æ®ï¼Œç›´æ¥é€‰æ‹©ç¬¬ä¸€ä¸ªåˆ†ç±»
    if (this.categories.length > 0) {
      // é¦–å…ˆæ£€æŸ¥URLä¸­æ˜¯å¦æœ‰åˆ†ç±»IDå‚æ•°
      const categoryId = parseInt(this.$route.params.id);
      if (categoryId) {
        this.selectCategory(categoryId);
      } else {
        // å¦‚æœæ²¡æœ‰URLå‚æ•°ï¼Œé€‰æ‹©ç¬¬ä¸€ä¸ªåˆ†ç±»
        const defaultCategory = this.categories[0];
        this.selectCategory(defaultCategory.id);
      }
    } else {
      // ä»¥é˜²ä¸‡ä¸€ï¼Œå¦‚æœæ²¡æœ‰åˆ†ç±»æ•°æ®ï¼Œä»ç„¶å°è¯•è·å–
      this.fetchCategories();
    }
  },
  methods: {
    // é‡ç½®ç­›é€‰å’Œæ’åºé€‰é¡¹
    resetFilters() {
      this.filters.contentType = 'all';
      this.filters.search = '';
      this.sortBy = 'newest';
      this.currentPage = 1; // é‡ç½®ä¸ºç¬¬ä¸€é¡µ
    },
    
    // é€‰æ‹©åˆ†ç±»æ—¶é‡ç½®ç­›é€‰å’Œæ’åº
    selectCategory(category) {
      const categoryId = category.id || category; // æ”¯æŒä¼ å…¥åˆ†ç±»å¯¹è±¡æˆ–ID
      this.loading = true;
      this.resetFilters(); // é‡ç½®ç­›é€‰æ¡ä»¶
      this.currentPage = 1; // é‡ç½®ä¸ºç¬¬ä¸€é¡µ
      
      const selectedCategory = this.categories.find(cat => cat.id === categoryId);
      if (!selectedCategory) {
        this.loading = false;
        return;
      }
      
      // æ›´æ–°å½“å‰é€‰ä¸­çš„åˆ†ç±»ä¿¡æ¯
      this.selectedCategoryId = categoryId;
      this.categoryName = selectedCategory.name;
      
      // æ›´æ–°URLå‚æ•°ï¼Œä¿æŒå¯¼èˆªä¸€è‡´æ€§ï¼Œä½†é¿å…é‡å¤å¯¼èˆªåˆ°åŒä¸€é¡µé¢
      const currentCategoryId = parseInt(this.$route.params.id);
      if (currentCategoryId !== categoryId) {
        this.$router.push({ path: `/category/${categoryId}` });
      }
      
      // ä½¿ç”¨æ¨¡æ‹Ÿæ•°æ®ï¼Œå› ä¸ºæˆ‘ä»¬æ²¡æœ‰çœŸå®çš„API
      setTimeout(() => {
        try {
          // ä¸ºåˆ†ç±»æ·»åŠ æ›´å¤šç»Ÿè®¡ä¿¡æ¯
          this.enrichCategoryData(selectedCategory);
          
          // ç”Ÿæˆæ¨¡æ‹Ÿæ•°æ®
          this.useMockDataForCategory(categoryId);
        } catch (error) {
          console.error('è·å–å†…å®¹å¤±è´¥:', error);
          // å³ä½¿å‡ºé”™ä¹Ÿè¦ä½¿ç”¨æ¨¡æ‹Ÿæ•°æ®
          this.useMockDataForCategory(categoryId);
        } finally {
          this.loading = false;
        }
      }, 300); // æ¨¡æ‹Ÿç½‘ç»œå»¶è¿Ÿ
    },
    
    // å¤„ç†æœç´¢è¾“å…¥
    handleSearch() {
      this.currentPage = 1; // æœç´¢æ—¶é‡ç½®ä¸ºç¬¬ä¸€é¡µ
    },
    
    // ç›‘å¬ç­›é€‰æˆ–æ’åºå˜åŒ–æ—¶é‡ç½®é¡µç 
    watchFiltersOrSort() {
      this.currentPage = 1;
    },
    
    // è·å–åˆ†ç±»åˆ—è¡¨
    fetchCategories() {
      this.loading = true;
      try {
        // è¿™é‡Œæ˜¯æ¨¡æ‹ŸAPIè°ƒç”¨ï¼Œå®é™…é¡¹ç›®ä¸­åº”æ›¿æ¢ä¸ºçœŸå®API
        // ç”±äºæˆ‘ä»¬å·²ç»åœ¨dataä¸­å®šä¹‰äº†åˆå§‹categoriesï¼Œè¿™é‡Œå¯ä»¥ç›´æ¥ä½¿ç”¨
        // æ¨¡æ‹ŸAPIå»¶è¿Ÿï¼Œå¢å¼ºçœŸå®æ„Ÿ
        setTimeout(() => {
          this.loading = false;
          
          // å¦‚æœæœ‰URLå‚æ•°ï¼Œè‡ªåŠ¨é€‰æ‹©å¯¹åº”åˆ†ç±»
          const categoryId = parseInt(this.$route.params.id);
          if (categoryId) {
            this.selectCategory(categoryId);
          } else if (this.categories.length > 0 && !this.selectedCategoryId) {
            // å¦‚æœæ²¡æœ‰URLå‚æ•°ä¸”æœªé€‰æ‹©åˆ†ç±»ï¼Œé»˜è®¤é€‰æ‹©ç¬¬ä¸€ä¸ªåˆ†ç±»
            this.selectCategory(this.categories[0].id);
          }
        }, 300);
      } catch (error) {
        console.error('è·å–åˆ†ç±»åˆ—è¡¨å¤±è´¥:', error);
        this.error = 'è·å–åˆ†ç±»åˆ—è¡¨å¤±è´¥';
        this.loading = false;
        
        // é”™è¯¯æ—¶ä»ç„¶ä½¿ç”¨åˆå§‹æ•°æ®
        if (this.categories.length > 0 && !this.selectedCategoryId) {
          this.selectCategory(this.categories[0].id);
        }
      }
    },
    
    async fetchTopicsByCategory(categoryId) {
      try {
        this.loading = true;
        
        // è·å–å½“å‰åˆ†ç±»ä¿¡æ¯
        const selectedCategory = this.categories.find(cat => cat.id === categoryId);
        const categoryName = selectedCategory?.name;
        
        // å¹¶è¡Œè¯·æ±‚è¯é¢˜å’Œåšå®¢æ•°æ®
        const [topicsResponse, blogsResponse] = await Promise.all([
          getTopicsByCategory(categoryId),
          getBlogList()
        ]);
        
        // ç­›é€‰è¯¥æ¥æºçš„è¯é¢˜
        this.topics = (topicsResponse.data || []);
        
        // ç­›é€‰è¯¥æ¥æºçš„åšå®¢æ–‡ç« 
        this.blogs = (blogsResponse.data || []).filter(blog => 
          blog.source === categoryName
        );
        
        // æ›´æ–°å½“å‰é€‰ä¸­çš„åˆ†ç±»ä¿¡æ¯
        if (selectedCategory) {
          this.categoryName = selectedCategory.name;
          this.selectedCategoryId = categoryId;
          // æ›´æ–°URLå‚æ•°ï¼Œä¿æŒå¯¼èˆªä¸€è‡´æ€§ï¼Œä½†é¿å…é‡å¤å¯¼èˆªåˆ°åŒä¸€é¡µé¢
          const currentCategoryId = parseInt(this.$route.params.id);
          if (currentCategoryId !== categoryId) {
            this.$router.push({ path: `/category/${categoryId}` });
          }
          
          // ä¸ºåˆ†ç±»æ·»åŠ æ›´å¤šç»Ÿè®¡ä¿¡æ¯
          this.enrichCategoryData(selectedCategory);
        }
      } catch (error) {
        console.error('è·å–å†…å®¹å¤±è´¥:', error);
        // é”™è¯¯æ—¶ä½¿ç”¨æ¨¡æ‹Ÿæ•°æ®
        this.useMockDataForCategory(categoryId);
      } finally {
        this.loading = false;
        this.currentPage = 1; // åŠ è½½æ–°åˆ†ç±»æ—¶é‡ç½®é¡µç 
      }
    },
    
    // ä¸°å¯Œåˆ†ç±»æ•°æ®
    enrichCategoryData(category) {
      const allContents = this.filteredContents;
      const totalViews = allContents.reduce((sum, item) => sum + (item.views || 0), 0);
      const totalComments = allContents.reduce((sum, item) => sum + (item.comments || 0), 0);
      
      // è®¡ç®—æ´»è·ƒåº¦ (åŸºäºå†…å®¹æ•°é‡ã€æµè§ˆé‡å’Œè¯„è®ºæ•°)
      const contentScore = allContents.length * 10;
      const viewsScore = totalViews * 0.01;
      const commentsScore = totalComments * 2;
      const totalScore = contentScore + viewsScore + commentsScore;
      
      // é™åˆ¶æ´»è·ƒåº¦åœ¨0-100ä¹‹é—´
      const activity = Math.min(100, Math.max(0, totalScore));
      
      // æ›´æ–°åˆ†ç±»ä¿¡æ¯
      category.activity = activity;
      category.totalViews = totalViews;
      category.totalComments = totalComments;
    },
    
    // å½“APIè°ƒç”¨å¤±è´¥æ—¶ä½¿ç”¨æ¨¡æ‹Ÿæ•°æ®
    useMockDataForCategory(categoryId) {
      const selectedCategory = this.categories.find(cat => cat.id === categoryId);
      if (!selectedCategory) return;
      
      // ç”Ÿæˆæ¨¡æ‹Ÿè¯é¢˜æ•°æ®
      const mockTopics = this.generateMockTopics(selectedCategory.name, 5);
      // ç”Ÿæˆæ¨¡æ‹Ÿåšå®¢æ•°æ®
      const mockBlogs = this.generateMockBlogs(selectedCategory.name, 8);
      
      this.topics = mockTopics;
      this.blogs = mockBlogs;
      
      // æ›´æ–°åˆ†ç±»ä¿¡æ¯
      this.categoryName = selectedCategory.name;
      this.selectedCategoryId = categoryId;
    },
    
    // ç”Ÿæˆæ¨¡æ‹Ÿè¯é¢˜æ•°æ®
    generateMockTopics(categoryName, count) {
      const titles = [
        `${categoryName}é¢†åŸŸæœ‰å“ªäº›å€¼å¾—å…³æ³¨çš„æ–°è¶‹åŠ¿ï¼Ÿ`,
        `å¦‚ä½•å¿«é€Ÿå…¥é—¨${categoryName}ç›¸å…³æŠ€æœ¯ï¼Ÿ`,
        `${categoryName}ä»ä¸šè€…çš„æ—¥å¸¸å·¥ä½œæ˜¯æ€æ ·çš„ï¼Ÿ`,
        `åœ¨${categoryName}é¡¹ç›®ä¸­é‡åˆ°çš„æŒ‘æˆ˜ä¸è§£å†³æ–¹æ¡ˆ`,
        `${categoryName}å­¦ä¹ èµ„æºæ¨è`,
        `åˆ†äº«ä½ çš„${categoryName}å­¦ä¹ ç»éªŒ`,
        `${categoryName}æŠ€æœ¯æ ˆå¯¹æ¯”åˆ†æ`
      ];
      
      return Array.from({ length: count }, (_, index) => ({
        id: `mock-topic-${Date.now()}-${index}`,
        title: titles[index % titles.length],
        category: categoryName,
        source: categoryName,
        views: Math.floor(Math.random() * 1000) + 100,
        comments: Math.floor(Math.random() * 50) + 5,
        createdAt: new Date(Date.now() - Math.random() * 30 * 24 * 60 * 60 * 1000).toISOString(),
        updatedAt: new Date(Date.now() - Math.random() * 7 * 24 * 60 * 60 * 1000).toISOString()
      }));
    },
    
    // ç”Ÿæˆæ¨¡æ‹Ÿåšå®¢æ•°æ®
    generateMockBlogs(categoryName, count) {
      const titles = [
        `${categoryName}å®æˆ˜ï¼šä»é›¶å¼€å§‹æ­å»ºé¡¹ç›®`,
        `${categoryName}æ ¸å¿ƒæ¦‚å¿µè¯¦è§£`,
        `${categoryName}æ€§èƒ½ä¼˜åŒ–æŠ€å·§æ€»ç»“`,
        `${categoryName}æœ€ä½³å®è·µåˆ†äº«`,
        `${categoryName}å¸¸è§é—®é¢˜ä¸è§£å†³æ–¹æ¡ˆ`,
        `${categoryName}é«˜çº§ç‰¹æ€§æ·±å…¥è§£æ`,
        `${categoryName}æ¶æ„è®¾è®¡æ¡ˆä¾‹åˆ†æ`,
        `å¦‚ä½•åœ¨å›¢é˜Ÿä¸­æ¨å¹¿${categoryName}æŠ€æœ¯`
      ];
      
      const authors = ['æŠ€æœ¯ä¸“å®¶', 'é«˜çº§å·¥ç¨‹å¸ˆ', 'æ¶æ„å¸ˆ', 'å¼€å‘ç»ç†', 'æŠ€æœ¯åšä¸»'];
      const tags = ['æŠ€æœ¯åˆ†äº«', 'å®æˆ˜ç»éªŒ', 'æœ€ä½³å®è·µ', 'æ€§èƒ½ä¼˜åŒ–', 'æ¶æ„è®¾è®¡', 'å…¥é—¨æ•™ç¨‹'];
      
      return Array.from({ length: count }, (_, index) => {
        // ç”Ÿæˆéšæœºå†…å®¹
        const paragraphs = Math.floor(Math.random() * 3) + 2;
        let content = '';
        for (let i = 0; i < paragraphs; i++) {
          content += `è¿™æ˜¯å…³äº${categoryName}çš„ç¬¬${i+1}æ®µå†…å®¹ã€‚æœ¬æ–‡å°†è¯¦ç»†ä»‹ç»${categoryName}çš„ç›¸å…³çŸ¥è¯†å’Œå®è·µç»éªŒï¼Œå¸®åŠ©è¯»è€…æ›´å¥½åœ°ç†è§£å’Œåº”ç”¨è¿™é¡¹æŠ€æœ¯ã€‚\n\n`;
        }
        
        return {
          id: `mock-blog-${Date.now()}-${index}`,
          title: titles[index % titles.length],
          content: content,
          author: authors[index % authors.length],
          category: categoryName,
          tags: this.getRandomTags(tags, 3),
          source: categoryName,
          views: Math.floor(Math.random() * 2000) + 200,
          comments: Math.floor(Math.random() * 100) + 10,
          createdAt: new Date(Date.now() - Math.random() * 60 * 24 * 60 * 60 * 1000).toISOString(),
          updatedAt: new Date(Date.now() - Math.random() * 14 * 24 * 60 * 60 * 1000).toISOString()
        };
      });
    },
    
    // è·å–éšæœºæ ‡ç­¾
    getRandomTags(allTags, count) {
      const shuffled = [...allTags].sort(() => 0.5 - Math.random());
      return shuffled.slice(0, count);
    },
    
    // ä¸Šä¸€é¡µ
    prevPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
        this.scrollToTop();
      }
    },
    
    // ä¸‹ä¸€é¡µ
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
        this.scrollToTop();
      }
    },
    
    // è·³è½¬åˆ°æŒ‡å®šé¡µé¢
    goToPage(page) {
      if (page >= 1 && page <= this.totalPages) {
        this.currentPage = page;
        this.scrollToTop();
      }
    },
    
    // æ»šåŠ¨åˆ°é¡µé¢é¡¶éƒ¨
    scrollToTop() {
      window.scrollTo({ top: 0, behavior: 'smooth' });
    },

    
    viewContent(item) {
      // æ ¹æ®å†…å®¹ç±»å‹è·³è½¬åˆ°ä¸åŒçš„è¯¦æƒ…é¡µ
      if (item.type === 'blog') {
        console.log('æŸ¥çœ‹åšå®¢æ–‡ç« :', item.title);
        // å®é™…é¡¹ç›®ä¸­å¯ä»¥è·³è½¬åˆ°åšå®¢è¯¦æƒ…é¡µ
        // this.$router.push(`/blog/${item.id}`);
        alert(`æ‚¨é€‰æ‹©äº†åšå®¢æ–‡ç« ï¼š${item.title}`);
      } else {
        console.log('æŸ¥çœ‹è¯é¢˜:', item.title);
        // å®é™…é¡¹ç›®ä¸­å¯ä»¥è·³è½¬åˆ°è¯é¢˜è¯¦æƒ…é¡µ
        // this.$router.push(`/topic/${item.id}`);
        alert(`æ‚¨é€‰æ‹©äº†è¯é¢˜ï¼š${item.title}`);
      }
    },
    
    // è·å–å†…å®¹æ‘˜è¦
    getExcerpt(content) {
      if (!content) return '';
      // ç§»é™¤å¯èƒ½çš„æ¢è¡Œç¬¦å¹¶æˆªå–å‰100ä¸ªå­—ç¬¦
      const plainText = content.replace(/\n/g, ' ').trim();
      return plainText.length > 100 ? plainText.substring(0, 100) + '...' : plainText;
    },
    
    // æ ¼å¼åŒ–æ•°å­—ï¼ˆå¦‚ï¼š1234 -> 1.2kï¼‰
    formatNumber(num) {
      if (num >= 10000) {
        return (num / 10000).toFixed(1) + 'w';
      } else if (num >= 1000) {
        return (num / 1000).toFixed(1) + 'k';
      }
      return num.toString();
    },
    
    // æ ¼å¼åŒ–æ—¥æœŸ
    formatDate(dateString) {
      const date = new Date(dateString);
      const now = new Date();
      const diff = now - date;
      
      // è®¡ç®—ä¸åŒæ—¶é—´å•ä½
      const seconds = Math.floor(diff / 1000);
      const minutes = Math.floor(seconds / 60);
      const hours = Math.floor(minutes / 60);
      const days = Math.floor(hours / 24);
      
      // æ ¹æ®æ—¶é—´å·®è¿”å›ä¸åŒçš„æ˜¾ç¤ºæ ¼å¼
      if (days > 0) {
        return `${days}å¤©å‰`;
      } else if (hours > 0) {
        return `${hours}å°æ—¶å‰`;
      } else if (minutes > 0) {
        return `${minutes}åˆ†é’Ÿå‰`;
      } else {
        return 'åˆšåˆš';
      }
    }
  }
};
</script>

